@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-size: 18px;
  font-family: 'Public Sans', sans-serif;
  margin: 0;
  padding: 0;
}
/*# sourceMappingURL=App.css.map */
.Nav nav ul {
  background-color: white;
  width: 100%;
  min-height: 70px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: center;
  list-style: none;
}

.Nav nav ul li {
  cursor: pointer;
}

.Nav nav ul .pages li:hover {
  color: #2d314d;
  border-bottom: 4px solid #31d35c;
  padding-bottom: 10px;
}

.Nav nav ul .request {
  border: 0;
  border-bottom: 0;
  border-top: 0;
  border-radius: 50px;
  padding: 5% 10%;
  background-image: linear-gradient(to right, #31d35c, #2bb7da);
  font-weight: 400;
  color: white;
  cursor: pointer;
  outline: none;
}

.Nav nav ul .request:hover {
  opacity: 0.5;
}

.Nav nav ul .pages {
  display: flex;
  flex-direction: row;
}

.Nav nav ul .pages li {
  margin-left: 15px;
  color: #9698a6;
}

.Nav .mobileNav {
  display: none;
}

.Nav .mobileMenu {
  display: none;
}

@media screen and (max-width: 700px) {
  .Nav .desktopNav {
    display: none;
  }
  .Nav .mobileNav {
    display: flex;
  }
  .Nav .mobileNav ul {
    justify-content: space-between;
    margin: 10px;
  }
  .Nav .mobileMenu {
    background-color: white;
    width: 50%;
    border-radius: 5px;
    border: 3px solid #f3f4f6;
    position: absolute;
    top: auto;
    left: 0;
    right: 0;
    bottom: auto;
    margin: auto;
  }
  .Nav .mobileMenu ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    list-style: none;
    color: #9698a6;
  }
  .Nav .mobileMenu ul li {
    margin: 10px;
    cursor: pointer;
  }
  .Nav .mobileMenu ul li:hover {
    color: #2d314d;
  }
}
/*# sourceMappingURL=Nav.css.map */
.Header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Header .left {
  width: 50%;
  margin-top: 10%;
}

.Header .left h1 {
  color: #2d314d;
  font-size: 45px;
  font-weight: 400;
}

.Header .left p {
  color: #9698a6;
}

.Header .left button {
  margin: 10px 0;
  border: 0;
  color: white;
  font-weight: 700;
  background-image: linear-gradient(to right, #31d35c, #2bb7da);
  border-radius: 50px;
  border-bottom: 0;
  border-top: 0;
  padding: 3%;
  cursor: pointer;
  outline: none;
}

.Header .left button:hover {
  opacity: 0.5;
}

@media screen and (min-width: 701px) {
  .left {
    margin: 0 10%;
  }
  .right {
    width: 40%;
    background-image: url(/easybank/static/media/bgintrodesktop.9cea2f98.svg);
  }
  .right img {
    width: 100%;
  }
}

@media screen and (max-width: 700px) {
  .Header {
    flex-direction: column-reverse;
    justify-content: center;
  }
  .left {
    margin: 0 auto;
  }
  .right {
    width: 100%;
    background-image: url(/easybank/static/media/bgintrodesktop.9cea2f98.svg);
  }
  .right img {
    width: 100%;
  }
}
/*# sourceMappingURL=Header.css.map */
.Reasons {
  background-color: #fafafa;
  width: 100%;
  min-height: 650px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
}

.Reasons .intro {
  padding: 5% 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 50%;
}

.Reasons .intro h1 {
  color: #2d314d;
  font-size: 35px;
  font-weight: 400;
}

.Reasons .intro p {
  color: #9698a6;
}

.Reasons .reasonswhy h3 {
  font-size: 20px;
  font-weight: 400;
  color: #2d314d;
  margin: 10px 0;
}

.Reasons .reasonswhy p {
  color: #9698a6;
}

@media screen and (min-width: 701px) {
  .intro {
    margin: 10%;
  }
  .reasonswhy {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .reasonswhy div {
    width: 20%;
  }
}

@media screen and (max-width: 700px) {
  .intro {
    margin: 0 auto;
  }
  .reasonswhy {
    display: flex;
    flex-direction: column;
  }
  .reasonswhy div {
    width: 50%;
    margin: 10px auto;
  }
  .reasonswhy div img {
    margin: 0 auto;
  }
}
/*# sourceMappingURL=Reasons.css.map */
.Articles {
  padding: 5% 0;
  background-color: #f3f4f6;
  width: 100%;
}

.Articles h1 {
  margin: 10px;
  font-size: 40px;
  font-weight: 400;
  color: #2d314d;
}

.Articles .article {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.Articles .article .art {
  display: flex;
  flex-direction: column;
  width: 230px;
  background-color: white;
  margin: 10px;
  border-radius: 10px;
  cursor: pointer;
}

.Articles .article .art img {
  width: 100%;
  border-radius: 10px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.Articles .article .art h3 {
  padding: 10px;
  font-size: 25px;
  font-weight: 400;
  color: #2d314d;
}

.Articles .article .art p {
  padding: 10px;
  font-size: 15px;
  color: #9698a6;
}

.Articles .article h3:hover {
  color: #31d35c;
}
/*# sourceMappingURL=Articles.css.map */
.Footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
  min-height: 250px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  background-color: #2d314d;
  color: white;
  align-items: center;
}

.Footer .first {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.Footer .first span {
  margin: 5px;
  cursor: pointer;
}

.Footer .first span:hover {
  color: #31d35c;
}

.Footer .first div {
  margin: 10px;
}

.Footer .second div {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.Footer .second li {
  margin: 20px;
  font-weight: 300;
  cursor: pointer;
}

.Footer .second li:hover {
  color: #31d35c;
}

.Footer .third {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.Footer .third button {
  border: 0;
  border-bottom: 0;
  border-top: 0;
  border-radius: 50px;
  color: white;
  padding: 5%;
  background-image: linear-gradient(to right, #31d35c, #2bb7da);
  cursor: pointer;
}

.Footer .third button:hover {
  opacity: 0.5;
}

.Footer .third p {
  margin: 20px 0;
  font-weight: 300;
}

@media screen and (min-width: 701px) {
  .second {
    display: flex;
    flex-direction: row;
    list-style: none;
    flex-wrap: wrap;
  }
  .second div {
    margin: 10px;
  }
}

@media screen and (max-width: 700px) {
  .second {
    list-style: none;
    width: 100%;
    text-align: center;
    flex-direction: column;
  }
  .second div {
    margin: 0;
  }
}
/*# sourceMappingURL=Footer.css.map */
